/* Contact */

#contact-text p {
    margin: 0 30px 30px;
    width: auto;
}

#contact-form {
    display: -ms-grid;
    display: grid;
    text-align: center;
}

#name {
    margin: 0 5px 5px;
}

#email {
    margin: 5px;
}

#email,
#name {
    padding: 10px;
    height: 18px;
    border-radius: 3px;
    border-style: none;
    background: var(--modal-panel-background-color);
    border-width: 1px;
    font-size: 16px;
    font-family: "Raleway", sans-serif;
}

#message,
#submit-button {
    border-style: none;
    font-size: 16px;
    font-family: "Raleway", sans-serif;
}

#message {
    vertical-align: top;
    padding: 10px 10px 86px;
    margin: 5px;
    height: 18px;
    border-radius: 3px;
    background: var(--modal-panel-background-color);
    border-width: 1px;
    resize: none;
}

.contact-form-text-area {
    width: 500px;
    max-width: 70vw;
}

#submit-button {
    background: #dc416a;
    color: #fff;
    width: 100px;
    height: 38px;
    margin: 30px 5px 30px;
    cursor: pointer;
}

#submit-button:hover {
    transition: .3s;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}